import React from "react";

const SkillsTab = ({ image }) => {
  // Adjust the styles for the SkillsTab
  const tabStyle = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '400px',  // Fixed height
    width: '700px',   // Fixed width
    margin: '0 10px', // Adds horizontal space between items
    flexShrink: 0,    // Prevents the item from shrinking
    backgroundColor: '#718096', // Slate color
    borderRadius: '1rem',
    overflow: 'hidden', // Keeps the image within the bounds of the border radius
  };

  // Adjust the image style to maintain aspect ratio
  const imageStyle = {
    height: '100%',
    width: '100%',
    objectFit: 'cover', // Adjust as needed, could also be 'contain' if you don't want to crop the images
  };

  return (
    <div style={tabStyle}>
      <img src={image} alt="Skill" style={imageStyle} />
    </div>
  );
};

export default SkillsTab;
